<template >

    <header class="header">
        <Transition name="notes">
            <div class="header__notes" v-show="header">
                <button class="header__lang">uz</button>
                <h1 class="header__title">Eslatmalar</h1>
                <button class="header__search" @click="header = !header">
                    <img src="@/assets/img/lupa.svg" alt="">
                </button>

            </div>
        </Transition>
        <Transition name="form">
            <div class="header__form" v-show="!header">
                <button class="header__back" @click="header = !header, search = null">
                    <img src="@/assets/img/back.svg" alt="">
                </button>
                <input type="text" class="header__input" placeholder="Qidirish..." v-model="search">
                <button class="header__close" @click="search = null">
                    <img src="@/assets/img/stop.svg" alt="">
                </button>
            </div>
        </Transition>
    </header>


</template>


<script>

export default {
    data() {
        return {
            header: true,
            search: null
        }
    }
}
</script>
